:root {
  --blue-color: rgb(49, 49, 238);
  --red-color: rgb(230, 47, 47);
  --green-color: rgb(129, 212, 61);
  --black-color: black;

  --heading-color: black;
  --text-color: rgb(73, 73, 73);

  --backgound-color: beige;

  --font-size-large: 24px;
  --font-size-medium: 20px;
  --font-size-normal: 16px;

  --primary-color: rgb(37, 37, 219);
  
  --text-dark: rgb(8, 8, 8);
  --text-lite:  #eee;

  /* color templates */
  --dark: #05386B;
  --seconde-dark: #379683;

  --background: #5cdb95;
  --seconde-background: #8EE4Af;
  
  --heading: #edf5e1;
  --text: #05386b; 
  --heading-bg: #f64c72;

  /* font Global  */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;

}
/* global font style */
h1 {
  font-weight: 600;
  font-size: 28px;
}
h2 {
  font-weight: 600;
  font-size: 24px;
  padding: 0px;
  margin: 5px;
}
h3 {
  font-weight: 500;
  font-size: 20px;
}
ul li:first-child {
  font-weight: 600;
}

/* global headign */
.heading-container {
  display: flex;
  justify-content: center;
  align-items:center;
  /* background-color: blue; */
  margin-top: 25px;
  margin-bottom: 10px;
}
.heading {
  background-color: var(--heading-bg);
  color: var(--heading);
  padding: 5px;
  border-radius: 5px;
  min-width: 300px;
  text-align: center;
}
/* ------Header------- */
header {
  display: flex;
  background-color: var(--background);
  color: var(--text);
  height: 12vh;
  position: sticky;
  left: 0px;
  right: 0px;
  top: 0px;
  z-index: 200;
}
.logo-text-container {
  flex-basis: 1;
  display: flex;
  align-items: center;
}
.logo {
 height: 60px;
 width: 60px;
 background-color: var(--background);
}
.logo img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.navbar-item-container {
  display: flex;
  list-style: none;
  /* justify-content: ; */
  align-items: center;
  padding: 0px;
 
}
.navbar-item {
  padding-left: 20px;
  font-size: var(--font-size-medium);
  font-weight: 600;
  color: inherit;
}
.navbar-item:last-child {
  padding-right: 20px;
}
.navbar-item a {
  text-decoration: none;
  color: var(--text);
}
.navbar-item-container {
  margin-left: auto;
}
/* ------Header-End---------- */

/* ------Slider-------------- */
.slider-container {
  position:relative;
  /* height: 88vh; */
  overflow: hidden;
}
.slides {
  background-color: var(--heading);
  /* height: 88vh;
  width: 100vw; */
}
.slides img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.slider-wrapper {
  display: flex;
  overflow: hidden;
}
.slider {
  display: flex;
  transition: all .8s ease-out;
}
.left-arrow {
  position: absolute;
  top: 50%;
  left: 20px;
  height: 50px;
  width: 50px;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: var(--heading);
  opacity: .5;
  z-index: 100;
}
.right-arrow {
  position: absolute;
  top: 50%;
  right: 20px;
  height: 50px;
  width: 50px;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: var(--heading);
  opacity: .5;
  z-index: 100;
}
/* ------Slider End--------- */

/* ------Home page---------- */
/* Conching WE Provide */
.section-heading {
  text-align: center;
}
.section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  overflow: auto;
}
.service-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-right: 5px;
  padding-left: 5px ;
}
.service-box-title-box {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--dark);
  color: var(--heading);
  height: 120px;
  width: 120px;
  border-radius: 60px;
}
.service-box-des {
  font-size: var(--font-size-medium);
  font-weight: 600;
  text-align: center;
}
/* Conching WE Provide End */

/* subjects  */
 .sylabus-heading {
   text-align: center;
 }
 .section-box div {
  margin: 0px;
  padding: 0px;
}
.section2 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
 .section2 .section-box {
  display: flex;
  justify-content: space-around;
  width: 90vw;
  margin-bottom: 20px;
  
}
.section2 .box-left {
  text-align: center;
  padding: 10px;
  padding-right: 30px;
  padding-left: 30px;
  background-color: var(--dark);
  color: var(--heading);
}
.section2 .box-right{
  background-color: var(--heading);
  color: var(--text);
  padding-left: 50px;
  padding-top: 20px;
  
}
.sylabus-box {
  display: flex ;
  flex-wrap: wrap ;
}
.subjects {
  padding: 2px 10px 2px 10px;
  min-width: 60px;
  font-size: var(--font-size-medium);
  
}
.section2 .image img {
  height: 200px;
  width: 200px;
}
@media (min-width: 1200px) {
  .section2 .box-right {
    padding-right: 200px ;
  }
  .subjects {
    line-height: 2.5rem;
  }
  
}
/* section3 facalty */
 .faculty-heading {
   text-align: center;
 }
 .section3 {
  display: flex;
  justify-content: space-evenly;
 }
 .section3 .wrapper {
   display: flex;
   flex-wrap: wrap;
   width: 90vw;
 }
 .section3 .item {
  width: 29vw;
  background-color: var(--dark);
  color: var(--heading);
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
}
.section3 .item:nth-child(3n+2) {
  margin-left: 1.5vw;
  margin-right: 1.5vw;
}
.section3 .item .image {
  height: 70%;
  width: 80%;
  max-width: 280px;
  max-height: 280px;
  align-self: center;
}
.section3 .item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.section3 .item h2,p {
  align-self: center;
  padding: 0px;
  margin: 0px;
}
.section3 .subject-box {
  align-self: center;
  margin: 0px;
}
/* .section3 .subject-box p:first-child {
  font-weight: 500;
  font-size: var(--font-size-medium);
} */
.section3 .subject-box p {
  font-size: 17px;
  padding-bottom: 10px;
}
.section3 .item img:hover {
  transform: scale(1.1);
  box-shadow: 1px 10px 20px black;
}

/* Footer section */
.footer-section {
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: var(--background);
}
.footer-section-box {
  min-width: 170px;
}
.footer-section ul {
  list-style: none;
  padding-left: 10px;
  padding-right: 10px;
}
.footer-section ul li {
  color: var(--text);
  line-height: 1.7em;
  padding-left: 10px;
}
.footer-section ul li:first-child {
  border-bottom: 2px solid black;
  font-size: var(--font-size-medium);
  padding-left: 0px;
}

/* hide mobile minu and Icon in Pc   */
.mobile-menu, .mobile-menu-icon {
  display: none;
}

/* Responsive  for mobile*/
@media (max-width: 576px) {
   .logo-text-container h1 {
    font-size: var(--font-size-large);
  }
  .navbar-item-container .navbar-item {
    display: none;
  }
  .mobile-menu-icon {
    display: block;
    align-self: center;
    margin-right: 5px;
  }
  .mobile-menu {
    display: block;
    position: fixed;
    background-color: var(--dark);
    top: 10vh;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100vh;
  }
  .mobile-menu-navbar-item-container {
    list-style: none;
    padding: 0px;
  }
  .mobile-menu-navbar-item-container li {
    font-size: var(--font-size-medium);
    border-bottom: 2px solid var(--background);
  }
  .mobile-menu-navbar-item-container li a {
    display: block; 
    height: 100%;
    width: 100%;
    padding-top:5px ;
    padding-bottom: 5px;
    color: var(--heading);
  }
  .service-box {
    min-width: 180px;
    min-height: 130px;
    padding: 1px 5px 1px 5px;
    position: relative;
  }
  .service-box-title-box {
    width: 90px;
    height: 90px;
    border-radius: 50px;
    position: absolute;
    top: 1px;

  }
  .service-box-title-box h1 {
    font-size: 18px;
    padding: 0px;
    margin: 0px;
  }
  .service-box-des {
    font-size: 12px;
    position: absolute;
    bottom: 8px;
  }
  /* subjects */
  .section2 {
   overflow: hidden;
  }
  .section-box{
    flex-direction: column;
  }
  .section2 .box-left {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 5px 0px 0px 5px;

  }
  .section2 .image img {
    width: 150px;
    height: 150px;
    overflow: hidden;
  }
  .section2 .image {
    width: 150px;
    height: 150px;
    overflow: hidden;
    object-fit: cover;
  }
  .section2 .box-left-title-des {
    margin-left:auto ;
    margin-right: auto;
   display: flex;
   flex-direction: column;
  }
  .section2 .box-left-title-des h2 {
    margin: 0px;
  }
  .section2 .box-right {
    padding-left: 10px;
    padding-bottom: 20px;
  }
  .section2 .box-right h2 {
    padding-left: 7px;
  }
  /* faculty section */
  .section3 .item {
    width: 44vw;
  }
  .section3 .item:nth-child(3n+2) {
    margin-left: 0px;
    margin-right: 0px;
  }
  .section3 .item:nth-child(2n+1) {
    margin-right: 2vw;
  }
  .hide-first {
    display: none;
  }
  .hide-menu {
    animation: hide ease .8s ;
    animation-iteration-count : 1;
    animation-fill-mode: forwards;
  }
  .show-menu {
    animation: show ease .8s;
    animation-fill-mode: forwards;
  }
  @keyframes hide {
    0% {
        opacity: 1;
    }
    100% {
        transform: translateX(100vw);
        opacity: 0.6;
     }
}
  @keyframes show {
    0% {
      opacity: 0.6;
      transform: translateX(100vw);
    }
    100% {
      opacity: 1;
      transform: translateX(0vw);
    }
    
  }
  /* header */
  header {
    height: 10vh;
  }
  /* footer */
  .footer-section {
    justify-content: space-between;
  }

 
}


/* ---------ContactUs------------  */
.cu-section {
  overflow: hidden;
  display: flex;
}
.cu-section .cu-image {
  height: 400px;
  width: 1000px;
  margin-left: auto;
  margin-right: auto; 
}
.cu-section img {
  height: 100%;
  width: 100%;
  overflow: hidden;
  object-fit: cover;
}
.cu-section2 {
  display: flex;
  flex-direction: column; 
  align-items: center;
  justify-content: center;
}
.cu-section2 label, .cu-section2 input {
  display: block;
  padding: 10px;
  border-radius: 3px;
  width: 300px;
  font-size: 18px;
}
.cu-section2 label {
  font-weight: 600;
  font-size: 22px;
  letter-spacing: 1px;
  color: var(--heading);
}
.cu-section2 textarea {
 width: 950px;
  font-size: 18px;

}
.cu-section2-inner {
  width: 1000px;
  box-sizing: border-box;
  background-color: var(--dark);
  color: var(--heading);
  padding: 20px;
}
.cu-section3 {
  display: flex;
  align-items:center;
 justify-content: center;
}
.cu-setion3-inner {
 width: 1000px;
 box-sizing: border-box;
 background-color: var(--heading);  
 color: var(--dark);
 padding: 20px 0px 20px 0px;
 text-align: center;
}
.cu-setion3-inner p {
  display: inline-block;
  font-size: 24px ;
}
.cu-section2-inner .btn {
  font-size: 18px;
  padding: 8px 20px 8px 20px;
  cursor: pointer;
  margin-top: 20px;
}
.cu-section2-inner .hide-btn {
  display: none;
}
.show-info {
  display: block !important;
  text-align: center;
  animation: showinfo 2s ease 1 ;
}
@keyframes showinfo {
   0% {
     transform: translateY(-20px);
   }
   40% {
     transform: translateY(20px);
   }
   50% {
     transform: translateY(21px);
   }
   100% {
     transform: translateY(0px);
   }
}
/* ---Responsive ContactUs--- */
@media (max-width: 1000px) {
  .cu-section .cu-image {
    width: 98vw;
  }
  .cu-section2 label, .cu-section2 input {
    width: 200px;
    font-size: 16px;
  }
  .cu-section2 label {
    font-size: 20px;
  }
  .cu-section2 textarea {
    width: 96%;
    font-size: 16px;
  }
  .cu-section2-inner {
    width: 98vw;
  }
  .cu-setion3-inner {
    width: 98vw;
  }
  .cu-setion3-inner p {
    font-size: 20px;
  }
  .cu-section2-inner .btn {
    
  }
}